@use "sass:color";

// Export color variables
$primary-blue: #e6f3ff;
$secondary-blue: #b3d9ff;
$dark-blue: #0066cc;
$white: #ffffff;
$black: #000000;
$gray: #f5f5f5;

// Helper functions to replace deprecated ones
@function darken-compat($color, $amount) {
  @return color.adjust($color, $lightness: -$amount);
}
